<template>
  <div class="directory-page">
    <div class="navbar">
      <div class="container">
        <div class="logo">
          <span class="logo-icon">📚</span>
          <h1 @click="jumpto('/')">首页</h1>
        </div>
        <div class="nav-actions">
          <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
              <circle cx="11" cy="11" r="8"></circle>
              <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
            </svg>
        </div>
      </div>
    </div>

    <main class="main-content">
      <div class="container">
        <div class="section-header">
          <h3>文章索引</h3>
          <div class="section-divider"></div>
        </div>

        <div class="directory-content">
          <!-- 加载状态显示 -->
          <div v-if="isLoading" class="loading-state">
            <div class="loading-spinner"></div>
            <p>加载目录中...</p>
          </div>
          
          <!-- 目录内容 -->
          <div v-else class="directory-grid">
            <!-- 遍历目录结构 -->
            <div v-for="(item, name) in directoryStructure" :key="name" class="directory-item">
              <div v-if="item.type === 'fold'" class="folder-item">
                <div class="folder-header" @click="toggleFolder(name)">
                  <span class="folder-icon">{{ isFolderOpen[name] ? '📁' : '📂' }}</span>
                  <span class="folder-name">{{ name }}</span>
                  <span class="folder-count">({{ getFolderCount(item.content) }})</span>
                </div>
                <div v-if="isFolderOpen[name]" class="folder-content">
                  <div v-for="(subItem, subName) in item.content" :key="subName" class="sub-item">
                    <div v-if="subItem.type === 'fold'" class="sub-folder-item">
                      <div class="sub-folder-header" @click.stop="toggleSubFolder(name, subName)">
                        <span class="sub-folder-icon">{{ getSubFolderOpen(name, subName) ? '📁' : '📂' }}</span>
                        <span class="sub-folder-name">{{ subName }}</span>
                      </div>
                      <div v-if="getSubFolderOpen(name, subName)" class="sub-folder-content">
                        <div v-for="(subSubItem, subSubName) in subItem.content" :key="subSubName" class="file-item">
                          <a href="#" class="file-link" @click.prevent="gotoReader(`${name}/${subName}/${subSubName}`)">
                            <span class="file-icon">📄</span>
                            <span class="file-name">{{ subSubName }}</span>
                            <span class="file-date">{{ formatDate(subSubItem.time) }}</span>
                          </a>
                        </div>
                      </div>
                    </div>
                    <div v-else class="file-item">
                      <a href="#" class="file-link" @click.prevent="gotoReader(`${name}/${subName}`)">
                        <span class="file-icon">📄</span>
                        <span class="file-name">{{ subName }}</span>
                        <span class="file-date">{{ formatDate(subItem.time) }}</span>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else class="file-item">
                <a href="#" class="file-link" @click.prevent="gotoReader(name)">
                  <span class="file-icon">📄</span>
                  <span class="file-name">{{ name }}</span>
                  <span class="file-date">{{ formatDate(item.time) }}</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>

  </div>
</template>

<script setup>
import { ref, reactive, onMounted} from 'vue'
import { useRouter } from 'vue-router'

// 路由实例
const router = useRouter()

// 创建响应式变量存储目录结构数据
const directoryStructure = ref({})
const isLoading = ref(true)

// 从远程URL获取目录结构数据
const fetchDirectoryStructure = async () => {
  try {
    isLoading.value = true
    const response = await fetch('https://sometky.github.io/directory_structure.json')
    const data = await response.json()
    directoryStructure.value = data
  } catch (error) {
    console.error('获取目录结构失败:', error)
    // 可以添加错误处理逻辑，例如显示错误提示
  } finally {
    isLoading.value = false
  }
}

// 跳转路由
const jumpto = (path) => {
  router.push(path)
}

// 跳转到阅读器页面
const gotoReader = (filePath) => {
  filePath = "https://sometky.github.io/notes/" + filePath;
  router.push({ 
    path: '/articles/reader', 
    query: { file: filePath } 
  })
}

// 跟踪文件夹展开状态
const isFolderOpen = reactive({})
const subFolderOpenState = reactive({})

// 切换主文件夹展开状态
const toggleFolder = (folderName) => {
  isFolderOpen[folderName] = !isFolderOpen[folderName]
}

// 切换子文件夹展开状态
const toggleSubFolder = (parentFolder, subFolder) => {
  const key = `${parentFolder}_${subFolder}`
  subFolderOpenState[key] = !subFolderOpenState[key]
}

// 获取子文件夹展开状态
const getSubFolderOpen = (parentFolder, subFolder) => {
  const key = `${parentFolder}_${subFolder}`
  return subFolderOpenState[key] || false
}

// 计算文件夹中的项目数量
const getFolderCount = (content) => {
  let count = 0
  for (const key in content) {
    count++
  }
  return count
}

// 格式化日期
const formatDate = (dateString) => {
  if (!dateString) return ''
  // 处理 YYYYMMDD 格式
  if (dateString.length === 8) {
    const year = dateString.substring(0, 4)
    const month = dateString.substring(4, 6)
    const day = dateString.substring(6, 8)
    return `${year}-${month}-${day}`
  }
  return dateString
}

// 组件挂载时获取数据
onMounted(() => {
  fetchDirectoryStructure()
})
</script>

