import MainView from '@/mainview/MainView.vue'
import Lessons from '@/tools/Lessons.vue'
import TimeTable from '@/tools/TimeTable.vue'
import Content from '@/content/Content.vue'
import Reader from '@/content/Reader.vue'
import { createRouter, createWebHistory } from 'vue-router'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    { path: "/", component: MainView},
    { path: "/tools/lessons", component: Lessons},
    { path: "/tools/timetable", component: TimeTable},
    { path: "/articles/content", component: Content},
    { path: "/articles/reader", component: Reader}
  ],
})

export default router
