<script setup>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'

const router = useRouter()
const articles = ref([])
const directoryStructure = ref({})

const jumpto = (path) => {
  router.push(path)
}

// 从远程URL获取目录结构数据
const fetchDirectoryStructure = async () => {
  try {
    const response = await fetch('https://sometky.github.io/directory_structure.json')
    const data = await response.json()
    directoryStructure.value = data
    return data
  } catch (error) {
    console.error('获取目录结构失败:', error)
    // 如果远程获取失败，使用本地数据作为备选
    alert("获取目录结构失败，请检查网络连接")
  }
}

// 提取所有文件并按时间排序
const extractLatestArticles = async () => {
  const files = []
  const gradients = [
    'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
    'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
    'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)',
    'linear-gradient(135deg, #fa709a 0%, #fee140 100%)',
    'linear-gradient(135deg, #ff6b6b 0%, #556270 100%)'
  ]

  // 获取目录结构数据
  const structure = await fetchDirectoryStructure()

  // 递归遍历目录结构，收集文件
  const traverse = (structure, parentPath = '') => {
    for (const key in structure) {
      const item = structure[key]
      if (item.type === 'file') {
        files.push({
          name: key,
          time: item.time,
          url: item.url
        })
      } else if (item.type === 'fold' && item.content) {
        traverse(item.content, parentPath + key + '/')
      }
    }
  }

  traverse(structure)

  // 按时间排序，取最新的6个
  const sortedFiles = files.sort((a, b) => b.time.localeCompare(a.time)).slice(0, 6)

  // 转换为articles格式
  articles.value = sortedFiles.map((file, index) => {
    // 格式化日期 YYYYMMDD -> YYYY-MM-DD
    const formattedDate = file.time.replace(/(\d{4})(\d{2})(\d{2})/, '$1-$2-$3')
    // 去掉文件扩展名作为标题
    const title = file.name.replace(/\.[^/.]+$/, '')
    
    return {
      id: index + 1,
      title: title,
      date: formattedDate,
      url: file.url,
      gradient: gradients[index % gradients.length]
    }
  })
}

onMounted(() => {
  extractLatestArticles()
});</script>

